import React, { Component } from 'react'

import { AnimationOnScroll } from 'react-animation-on-scroll'
import './introduction.css'
class Introduction extends Component {
    render() {
        return (
            <React.Fragment>
                
                {/* Section 1 Hero introduction */}
                <div id="introduction" className="bg-colors d-flex">
                    <div className=" col-4 "></div>
                    <div className=" col-8 "></div>
                </div>

                <div className="main pt-md-5 pt-sm-5">
                        <div className="">
                            <div className="col-xl-12 intro-section d-flex flex-wrap align-items-center justify-content-center ">
                                <div className="col-lg-6 col-md-9 col-12 d-flex">
                                    <img className="profile-img col-lg-12 col-12" src='../images/pic2.png' alt=""/>
                                </div>
                                <AnimationOnScroll  animateIn="animate__zoomIn" duration={1.5} className="col-lg-6 col-md-9 col-12 hero-details">
                                    <h1 className=" col-xl-12 col-lg-8 col-12 customIntroColor"> <strong className="text-white">I'm Sudhir. A UI Developer based </strong>in India.</h1>
                                    <p className="text-white col-xl-12 col-lg-6 col-12 hero-detail "> I’m probably the most passionate designer you will ever get to work with. 
                                        If you have a great project that needs some amazing skills, I’m your guy.</p>  
                                </AnimationOnScroll>
                            </div>
                        </div>
                </div>
                
            </React.Fragment>
        )
    }
}

export default Introduction
