import React, { Component } from 'react'
import { AnimationOnScroll } from 'react-animation-on-scroll'
import './project.css'
class Projects extends Component {
  
    render() {
        return (
            <React.Fragment>
          <div id="my-work" className="bg-black">
              <div className="experience pt-lg-5">
                  <div className="container">
                    <div className="d-flex flex-wrap">
                        <div className="col-lg-6 col-12 px-2">
                            <AnimationOnScroll animateIn="animate__jackInTheBox" duration={1.5} className="p-5 pb-5 d-flex flex-column justify-content-center align-items-center">
                                <h3 className="lite-gray-color work-head-shadow">PROJECTS</h3>
                                <h2 className="word-head">PROJECTS</h2>
                            </AnimationOnScroll>
                            <AnimationOnScroll  animateIn="animate__fadeInLeft" duration={1.5}>

                                <img className="work-1" src="../images/weather-project.png" alt=""/>
                                <div className="projects-description custom-color">
                                    <div className="px-lg-5 p-2 pt-5 work-details">
                                        <h3 className="col-12 project-title">Live Weather Application</h3>
                                        <p className="col-12  lite-gray-color work-content">
                                            I worked with the guys at CBRE to redesign their entire website and mobile app fro both Android and iOS. 
                                            This project lasted for 4 months and was a very challenging one.
                                        </p>
                                    </div>
                                    <div className="d-flex justify-content-center py-3+">
                                        <a href="https://reactapplications.000webhostapp.com/build/" target="_blank" rel="noReferrer" > <button className="demo btn btn-outline-primary px-5">Demo</button></a>
                                    </div>
                                </div>
                            </AnimationOnScroll>
        
                            
                            <AnimationOnScroll className="pt-4" animateIn="animate__fadeInLeft" duration={1.5}>
                                <img className="work-2" src="../images/work-2.jpg"  alt=""/>
                                <div className="projects-description">
                                    <div className="px-lg-5 p-2 pt-5 work-details">
                                        <h3 className="col-12 text-white">Mobile Dashboard</h3>
                                        <p className="col-12 lite-gray-color work-content">
                                            I worked with the guys at CBRE to redesign their entire website and mobile app fro both Android and iOS.
                                            This project lasted for 4 months and was a very challenging one.
                                        </p>
                                    </div>
                                    <div className="d-flex justify-content-center py-3">
                                    <button className="demo btn btn-outline-primary px-5">Demo</button>
                                    </div>
                                </div>
                            </AnimationOnScroll>
                        </div>
                           
  
                       
  
                        <div className="col-lg-6 col-12 px-2 pt-lg-0 pt-4">
                              <div>
                                <AnimationOnScroll animateIn="animate__fadeInRight" duration={1}>
                                    <img className="work-3" src="../images/work-3.jpg" alt=""/>
                                    <div className="projects-description">
                                        <div className="px-lg-5 p-2 pt-5 work-details">
                                            <h3 className="col-12 text-white">CoinView Mobile App Design</h3>
                                            <p className="col-12 lite-gray-color work-content">
                                                I was commissioned by Coinview to revamp their mobile app. 
                                                I was asked to come up with a brand new design system and rethink the entire user expereince from the ground up.
                                            </p>
                                        </div>
                                        <div className="d-flex justify-content-center py-3">
                                            <button className="demo btn btn-outline-primary px-5">Demo</button>
                                        </div>
                                      </div>
                                </AnimationOnScroll>
  
                                  <AnimationOnScroll animateIn="animate__fadeInRight" duration={1.5} className="pt-4">
                                      <img className="work-4" src="../images/work-4.jpg"  alt=""/>
                                      <div className="projects-description">
                                          <div className="px-lg-5 p-2 pt-5 work-details">
                                              <h3 className="col-12 text-white">CoinView Mobile App Design</h3>
                                              <p className="col-12 lite-gray-color work-content">
                                                  I was commissioned by Coinview to revamp their mobile app. 
                                                  I was asked to come up with a brand new design system and rethink the entire user expereince from the ground up.
                                              </p>
                                          </div>
                                          <div className="d-flex justify-content-center py-3">
                                          <button className="demo btn btn-outline-primary px-5">Demo</button>
                                          </div>
                                      </div>

                                        <div className="custom-work-button d-flex justify-content-center align-items-center">
                                            <button className="btn btn-lg text-white button-stytle"> VIEW ALL PROJECTS </button>
                                        </div>
                                  </AnimationOnScroll>
                              </div>
                        </div>
                    </div>
                  </div>
              </div>
          </div>
            </React.Fragment>
        )
    }
}

export default Projects
