import React, { Component } from 'react'
import { AnimationOnScroll } from 'react-animation-on-scroll'
import './company.css'
class Company extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="bg-black py-lg-2">

                    <div className="d-flex flex-wrap justify-content-center align-items-center mx-auto" >
                        <AnimationOnScroll className="d-flex justify-content-center align-items-center col-lg-2 col-md-6 col-6 img1Company mt-lg-0 mt-lg-3 mt-3" animateIn="animate__fadeInLeftBig" duration={1}>
                            <a href="https://www.talentserve.org/" target="_blank" rel="noReferrer"><img className="talentServe" src="../images/TalentServe.png" alt=""/></a>
                        </AnimationOnScroll>
                        <AnimationOnScroll className=" col-lg-3 col-md-6 col-8 mt-2 mt-lg-3 " animateIn="animate__fadeInRightBig" duration={1}>
                            <a href="https://suryainformatics.com/" target="_blank" rel="noReferrer"><img className="suryaInfomatics" src="../images/surya.png" alt=""/></a>
                            
                        </AnimationOnScroll>
                    </div>
                    
                   
                </div>
            </React.Fragment>
        )
    }
}

export default Company
