import React, { Component } from 'react'
import { Animated } from 'react-animated-css'
import './navigation.css'
class Navigation extends Component {
    state={
        menu:'../logo/menu.png',
        close:'',
        animeStyleMenu:'animate__rotateIn',
        animeStyleClose:'',
        MenuVisible:true,
        closeVisible:false,
    }

    onClickToggler=()=>{

        if(this.state.menu==="../logo/menu.png"){
            this.setState({

                close:"../logo/close-cross.png",
                menu:"",MenuVisible:false,
                closeVisible:true,
                animeStyleMenu:'',
                animeStyleClose:'animate__rotateIn'

            })
        }
        else{
            this.setState({

                menu:"../logo/menu.png",
                close:"",
                MenuVisible:true,
                closeVisible:false,
                animeStyleClose:"",
                animeStyleMenu:"animate__rotateInDownLeft"
                
            })
        }
       
    }
    render() {
        return (
        <React.Fragment>
            
                {/* Nav Bar */}

            <nav className="navbar navbar-expand-lg custom-navColor fixed-top">
                <div className="container-fluid">
                <i className="fas fa-2x fa-user-graduate profile-icon"></i>
                        <button className="navbar-toggler custom-toggler"  type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="true" aria-label="Toggle navigation">
                            <div onClick={this.onClickToggler} className="d-flex align-items-center">
                                <Animated animationIn={this.state.animeStyleMenu} isVisible={this.state.MenuVisible}>
                                    <img src={this.state.menu} alt=''/>
                                </Animated>
                                <Animated animationInDuration={1500} animationIn={this.state.animeStyleClose} isVisible={this.state.closeVisible}>
                                    <img src={this.state.close} alt=''/>
                                </Animated>
                            </div>
                        </button>
                     <div className="custom-nav collapse navbar-collapse" id="navbarNav">
                        <div className="ms-auto">
                            <ul className="navbar-nav">
                                <li className="nav-item ">
                                    <a className="nav-link active custom-nav" aria-current="page" href="#experience">Experience</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link custom-nav" href="#my-work">Projects</a>
                                </li>
                                <li className="nav-item">
                                     <a className="nav-link custom-nav" href="#resume">Resume</a>
                                </li>
                                <li className="nav-item">
                                     <a className="nav-link custom-nav" href="#footer-Contact">Contact</a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </nav>
                
        </React.Fragment>
        )
    }
}

export default Navigation
