import React, { Component } from 'react'
import { AnimationOnScroll } from 'react-animation-on-scroll'
import './footer.css'
class Footer extends Component {
    state={
        whatsApp:'',
        instagram:'',
        linkedIn: '',
        gitHub:'',
        whatsAppTarget:'',
        instagramTarget:'',
        linkedInTarget:'',
        gitHubTarget:'',
    }

    onClickWhatApp=()=>{

        this.setState({whatsApp:'https://wa.link/zn1dwx',whatsAppTarget:'_blank'})

    }

    onClickInstagram=()=>{

        this.setState({instagram:'https://instagram.com/dum.code?utm_medium=copy_link',instagramTarget:'_blank'})

    }

    onClickLinkedIn=()=>{

        this.setState({linkedIn:'https://www.linkedin.com/in/sudhir-0706',linkedInTarget:'_blank'})

    }

    onClickGitHub=()=>{

        this.setState({gitHub:'https://github.com/Sudhirrr0706',gitHubTarget:'_blank'})

    }
    render() {
        return (
            <React.Fragment>
                <footer id="footer-Contact" className="footer-section">
                    <div className="container-fluid">
                        <AnimationOnScroll animateIn="animate__zoomIn" duration={1}  className="d-flex justify-content-center foot pt-5">

                            <a href={this.state.whatsApp} target={this.state.whatsAppTarget}> 
                                <i className="fab fa-2x fa-whatsapp mx-2 whatsApp" onClick={this.onClickWhatApp} ></i>
                            </a>

                            <a href={this.state.instagram} target={this.state.instagramTarget}>
                                <i className="fab fa-2x fa-instagram mx-2 instagram" onClick={this.onClickInstagram}></i>
                            </a>

                            <a href={this.state.linkedIn} target={this.state.linkedInTarget}>
                                <i className="fab fa-2x fa-linkedin mx-2 linkedIn"  onClick={this.onClickLinkedIn}></i>
                            </a>

                            <a href={this.state.gitHub} target={this.state.gitHubTarget}>
                                <i className="fab fa-2x fa-github-square mx-2 gitHub"  onClick={this.onClickGitHub}></i>
                            </a>
                        </AnimationOnScroll>
                        <div className="footer-link p-3">
                            <div className=" col-12">
                                <AnimationOnScroll animateIn="animate__zoomIn" duration={1}>
                                    <ul>
                                        <li className="py-2 pe-4"> <a className="" href="#introduction">Home</a></li>
                                        <li className="py-2 pe-4"> <a className="" href="#my-work">Work</a></li>
                                        <li className="py-2 pe-4"> <a className="" href="#about">About</a></li>
                                        <li className="py-2 pe-4"> <a className="" href="#skills">Progress</a></li>
                                    </ul>
                                </AnimationOnScroll>
                            </div>
                        </div>
                    </div>
                </footer>
            </React.Fragment>

        )
    }
}

export default Footer
