import React, { Component } from 'react'
import { AnimationOnScroll } from 'react-animation-on-scroll'
import './social.css'
class Social extends Component {
    render() {
        return (
           <React.Fragment>
                <div className="social-media">
                    <div className="d-flex flex-wrap-reverse justify-content-center align-items-center social-content">

                        <div className=" col-lg-6 col-12 p-lg-0 p-1">
                            <AnimationOnScroll animateIn="animate__zoomIn" duration={1} className="container py-5">
                                <AnimationOnScroll animateIn="animate__jackInTheBox" duration={1.5}>
                                    <h3 className="shadow">INSTAGRAM</h3>
                                    <h2 className="insta-head">INSTAGRAM</h2>
                                </AnimationOnScroll>
                                <p className="px-lg-3 mt-lg-5 social-innerContent">
                                If you area a person who enjoys photography, then I highly recommend that you check out my Instagram. 
                                I’m an avid traveller and I capture the best moments that I would love to cherish with the world.
                                </p>
                            </AnimationOnScroll>
                        </div>
      
                        <div className="col-lg-6 col-12 ">
                            <img className="SocialMedia"  src="../images/social.PNG" alt=""/>
                        </div>

                    </div>
                </div>

              
           </React.Fragment>
        )
    }
}

export default Social
