import React, { Component } from 'react'
// import { Animated } from 'react-animated-css'
import { AnimationOnScroll } from 'react-animation-on-scroll'
import './resume.css'
class Resume extends Component {
    state={
        link:'',
        targeted:''
    }
    onClickDownload=()=>{
        this.setState({
            link:'../Sudhir-Resume.pdf',
            targeted:'_blank'
        })
    }
    render() {
        return (
           <React.Fragment>
               <div id="resume" className=' bg-black p-lg-5 p-2 d-flex justify-content-center'>
                <div className="d-flex flex-wrap justify-content-center resume p-lg-5 px-lg-0 px-1 py-lg-0 py-5">
                    <div className="col-lg-6  col-12 resume-image">
                        <img className="" src="../images/resume.png" alt=""/>
                    </div>
                    <div className="resumes p-lg-4 pt-lg-5 pt-lg-0 col-lg-6 col-12 d-flex flex-wrap justify-content-center align-items-center ">

                        <AnimationOnScroll animateIn="animate__zoomIn" duration={1}>
                        <AnimationOnScroll animateIn="animate__jackInTheBox" duration={1.5} className="pt-lg-0 pt-2">
                            <h3 className="resume-shadow">RESUME</h3>
                            <h2 className="resume-head">RESUME</h2>
                        </AnimationOnScroll>
                            <p className="my-auto resume-content ">
                               Driven with passion and motivation to excel in the field of web development,my ultimate goal is to
                               become a successful web developer in the future.With consistency and hard work,I strongly believe that I will be 
                               able to achieve great things and come out with flying colors .
                               Take a look at my resume to know more about me and my thirst for success.  
                            </p>
                            <div  className="col-12 download mt-4">
                                <a href={this.state.link} target={this.state.targeted} onClick={this.onClickDownload}><i className="fas fa-download animation" /><p className=" pt-2">View!</p></a>
                            </div>
                        </AnimationOnScroll>
                            
                       

                    </div>
                </div>
               </div>
           </React.Fragment>
        )
    }
}

export default Resume
