import React, { Component } from 'react'
import { AnimationOnScroll } from 'react-animation-on-scroll'
import './skill.css'
class Skill extends Component {
    render() {
        return (
            <React.Fragment>
                <div id="skills" className="bg-black py-5">
                    <div className="skills-set d-flex flex-wrap container-fluid">
                        <div className="d-flex flex-wrap justify-content-center">
                            
                            <AnimationOnScroll animateIn="animate__zoomIn" duration={1} className="col-lg-4 col-md-7 col-12 d-flex flex-wrap justify-content-center" >
                               <AnimationOnScroll animateIn="animate__jackInTheBox" duration={1.5} className="d-flex flex-column align-items-center justify-content-center">
                                    <p className="skills-shadow">SKILLSET</p>
                                    <h2 className=" skill-set-head">SKILLSET</h2>
                               </AnimationOnScroll>
                                {/* <p className="lite-gray-color skill-inner col-lg-8 col-md-12 col-12">
                                    With skills in over 4 different fields of design, I am the perfect person to hire when it comes to a full fledged project. 
                                    Whatever your needs are, I can pretty much take on any challenge.
                                </p> */}
                            </AnimationOnScroll>
    
                            <div className="col-lg-8 col-md-10 col-12 skills container pt-md-2 pt-sm-2">
                                <div className="d-flex flex-wrap justify-content-evenly">
                                  
                                    <AnimationOnScroll animateIn="animate__zoomIn" duration={1} className="col-lg-5 col-md-8 col-12 mt-lg-5">
                                        <div className="d-flex justify-content-center">
                                            <img className="mb-3 px-2" src="../logo/html.png" alt=""/>
                                            <h2 className="html"><a href="https://www.w3schools.com/html/default.asp" target="_blank" rel="noReferrer">HTML5</a></h2>
                                        </div>
                                        <p className="mt-3 skill-description col-12">The HyperText Markup Language, or HTML is the standard markup language for documents designed to be displayed in a web browser.</p>
                                    </AnimationOnScroll>
                                      
                                    <AnimationOnScroll animateIn="animate__zoomIn" duration={1} className="col-lg-5 col-md-8 col-12 mt-4 mt-lg-5">
                                        <div className="d-flex justify-content-center">
                                            <img className="mb-3 px-2" src="../logo/css-3.png" alt=""/>
                                            <h2 className="css"><a href="https://www.w3schools.com/css/default.asp" target="_blank" rel="noReferrer">CSS3</a></h2>
                                        </div>
                                        <p className="mt-3 skill-description col-12">Cascading Style Sheets is a style sheet language used for describing the presentation of a document written in a markup language.</p>
                                    </AnimationOnScroll>
                                        
                                   
                                </div>
                      
                                <div className="d-flex flex-wrap justify-content-evenly">

                                    <AnimationOnScroll animateIn="animate__zoomIn" duration={1} className="col-lg-5 col-md-8 col-12 mt-4 mt-lg-5">
                                       <div className="d-flex justify-content-center">
                                            <img className="mb-3 px-2" src="../logo/bootstrap.png" alt=""/>
                                            <h2 className="bootstrap"><a href="https://getbootstrap.com/docs/5.0/getting-started/introduction/" target="_blank" rel="noReferrer">Bootstrap</a></h2>
                                       </div>
                                        <p className="mt-3 skill-description col-12">Bootstrap is a free and open-source CSS framework directed at responsive, mobile-first front-end web development.It contains CSS- and JavaScript-based design templates for interface components.</p>
                                    </AnimationOnScroll>
                                       
                                    <AnimationOnScroll animateIn="animate__zoomIn" duration={1} className="col-lg-5 col-md-8 col-12 mt-4 mt-lg-5">
                                        <div className="d-flex justify-content-center">
                                            <img className="mb-3 px-2" src="../logo/javascript.png" alt=""/>
                                            <h2 className="javascript"><a href="https://www.freecodecamp.org/learn/javascript-algorithms-and-data-structures/" target="_blank" rel="noReferrer">Javascript</a></h2>
                                        </div>
                                        <p className=" mt-3 skill-description col-12 px-lg-2">JavaScript, often abbreviated as JS, is a programming language that conforms to the ECMAScript specification. JavaScript is high-level, often just-in-time compiled, and multi-paradigm.</p>
                                    </AnimationOnScroll>
                                        
                                </div>

                                <div className="d-flex flex-wrap justify-content-evenly">

                                    <AnimationOnScroll animateIn="animate__zoomIn" duration={1} className="col-lg-5 col-md-8 col-12 mt-4 mt-lg-5">
                                        <div className="d-flex justify-content-center">
                                            <img className="mb-3 px-2" src="../logo/react.png" alt=""/>
                                            <h2 className="react"><a href="https://reactjs.org/" target="_blank" rel="noReferrer">ReactJS</a></h2>
                                        </div>
                                        <p className=" mt-3 skill-description col-12 ">React is a free and open-source front-end JavaScript library for building user interfaces or UI components. It can be used as a base in the development of single-page or mobile applications.</p>
                                    </AnimationOnScroll>
                                    



                                    <AnimationOnScroll animateIn="animate__zoomIn" duration={1} className="col-lg-5 col-md-8 col-12 mt-4 mt-lg-5">
                                        <div className="d-flex justify-content-center">
                                            <img className="mb-3 px-2" src="../logo/python.png" alt=""/>
                                            <h2 className="Python"><a href="https://www.w3schools.com/python/default.asp" target="_blank" rel="noReferrer">Python</a></h2>
                                        </div>
                                        <p className=" mt-3 skill-description">Python is an interpreted high-level general-purpose programming language. Its design philosophy emphasizes code readability with its use of significant indentation.</p>
                                    </AnimationOnScroll>
    
                                </div>


                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default Skill
