import React, { Component } from 'react'
import { AnimationOnScroll } from 'react-animation-on-scroll'
import './experience.css'
class Experience extends Component {
    render() {
        return (
            <React.Fragment>
               
                <div id="experience" className="bg-black work-section">
                    <div className="container py-5">
                        <div className="d-flex flex-wrap align-items-center">
                        
                            <div className="container py-5">

                                <AnimationOnScroll  animateIn="animate__jackInTheBox" duration={1.5}>
                                    <p className="head-shadow mx-auto">EXPERIENCE</p>
                                    <p className="mb-0 work-title lite-gray-color"><strong>EXPERIENCE</strong></p>
                                    
                                </AnimationOnScroll>
                                <AnimationOnScroll animateIn="animate__zoomIn" duration={1.5}>
                                    <h2 className="col-lg-7 col-md-10 col-12 text-white work-description mt-lg-3 mx-auto">Companies I have worked for in the past.</h2>
                                </AnimationOnScroll>

                            </div>
                            
                            <div className="d-flex flex-wrap justify-content-center pt-lg-5 inner-section py-5">

                              
                                <AnimationOnScroll animateIn="animate__zoomIn" duration={1.5} className="col-lg-6 col-md-8 col-12">
                                    <h2 className="mb-0 lite-gray-color num-head">01</h2>
                                    <p className="col-lg-7 col-md-10 col-12 text-white company-head"><a className='work1' href="https://www.talentserve.org/" target="_blank" rel="noReferrer">Talent Serve</a> , Interaction Designer (Internship)</p>
                                    <p className="col-lg-8 col-md-10 col-12 text-white work-detail">I'm currently , the lead designer on the interaction design team for Talent Serve.</p>
                                </AnimationOnScroll>
                                    
                              

                               
                                <AnimationOnScroll animateIn="animate__zoomIn" duration={1.5} className="col-lg-6 col-md-8 col-12">
                                    <h2 className="mb-0 lite-gray-color num-head">02</h2>
                                    <p className="col-lg-6 col-12 text-white company-head"><a className='work2' href="https://suryainformatics.com/" target="_blank" rel="noReferrer">Surya InforMatics</a> , UI Developer (Internship)</p>
                                    <p className="col-lg-9 col-12 text-white work-detail">I’d been a front end developer on a wide variety of internal skills for Surya InforMatics over the duration of 2 months.</p>
                                </AnimationOnScroll>
                           
                                
                            </div>
                        </div>
                    </div>
                    
                </div>
            
                
            </React.Fragment>
        )
    }
}

export default Experience
